.zoom-in {
  position: relative;
  display: inline-block;
  line-height: 0;
  /* Evita espacios extra debajo de la imagen */ }

/* Capa con el icono de la lupa (usando un pseudo-elemento ::after) */
.zoom-in::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.6);
  /* Fondo oscuro semitransparente */
  background-image: url('data:image/svg+xml,<svg xmlns="http://www.w3.org/2000/svg" width="48" height="48" viewBox="0 0 24 24" fill="none" stroke="white" stroke-width="2" stroke-linecap="round" stroke-linejoin="round"><circle cx="11" cy="11" r="8"></circle><line x1="21" y1="21" x2="16.65" y2="16.65"></line><line x1="11" y1="8" x2="11" y2="14"></line><line x1="8" y1="11" x2="14" y2="11"></line></svg>');
  background-repeat: no-repeat;
  background-position: center;
  background-size: 48px 48px;
  opacity: 0;
  /* Invisible por defecto */
  transition: opacity 0.3s ease-in-out;
  /* Transición suave */
  cursor: pointer; }

/* Muestra la capa al pasar el ratón por encima */
.zoom-in:hover::after {
  opacity: 1; }

